import ImageMap from "../Images";

const TITLE = {
    res: {
        r1: "",
        r2: "",
        r3: "",
        r4: "",
        r5: " ",
    }
}

const IMG = {
    res(className = "") {
        let t = TITLE.res[className];
        return <img width={16} height={16} src={ImageMap.pixel} alt={t} title={t} className={className} />;
    },
    clock() {
        return <img src={ImageMap.pixel} alt=" " title=" " className="clock" />;
    },
    warehouse() {
        return <img src={ImageMap.pixel} alt="" title="" className="warehouse" />;
    },
    granary() {
        return <img src={ImageMap.pixel} alt=" " title=" " className="granary" />;
    }
}

export { IMG, TITLE };