import React from 'react';
import { Link } from "react-router-dom";
import { Protected } from './AuthorizedContent';
import { OpenGuid } from "./Guid";
import Pure from './Pure';
import "./SideNavi.css";

class SideNavi extends Pure {
    render() {
        return <>
            <div className="side_navi">
                <p>
                    <Protected
                        authorized={<>
                            <Link to="/Home"> </Link>
                            <br />
                            <Link to="#" onClick={OpenGuid()}> </Link>
                            <Link to="/Profile"> </Link>
                            {/* <Link to="/Rules"></Link> */}
                            <br />
                            <Link to="/Login"><b> </b></Link>
                        </>}
                        else={<>
                            <Link to="/Home"> </Link>
                            <br />
                            <Link to="/Login"><b> </b></Link>
                            <Link to="/Register"><b> </b></Link>
                            <br />
                            <Link to="#" onClick={OpenGuid()}> </Link>
                            <Link to="/Rules"></Link>
                        </>}
                    />
                </p>
            </div>
        </>;
    }
}

export default SideNavi;